"use client";
import { useState } from "react";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Progress } from "@/components/ui/progress";
import { TrendingUp, Plus, Minus, AlertTriangle } from "lucide-react";

type GoalType = "quantitative" | "qualitative";

interface PerformanceGoal {
  id: string;
  type: GoalType;
  weight: number;
  specificGoal: string;
  achievementCriteria: string;
  methods: string;
}

export default function PerformanceGoals() {
  const [performanceGoals, setPerformanceGoals] = useState<PerformanceGoal[]>([]);

  const totalPerformanceWeight = performanceGoals.reduce((sum, goal) => sum + goal.weight, 0);
  const isWeightValid = totalPerformanceWeight === 100;

  const addGoal = (type: GoalType) => {
    const newGoal: PerformanceGoal = {
      id: `perf-${Date.now()}-${Math.random()}`,
      type,
      weight: 0,
      specificGoal: "",
      achievementCriteria: "",
      methods: ""
    };
    setPerformanceGoals([...performanceGoals, newGoal]);
  };

  const removeGoal = (id: string) => {
    setPerformanceGoals(performanceGoals.filter(goal => goal.id !== id));
  };

  const updateGoal = (id: string, field: keyof PerformanceGoal, value: any) => {
    setPerformanceGoals(performanceGoals.map(goal =>
      goal.id === id ? { ...goal, [field]: value } : goal
    ));
  };

  return (
    <div>
      <Card>
        <CardHeader>
          <div className="flex items-center space-x-3">
            <div className="p-2 rounded-lg bg-blue-100 text-blue-700">
              <TrendingUp className="w-5 h-5" />
            </div>
            <div>
              <CardTitle>業績目標</CardTitle>
              <p className="text-sm text-gray-600">定量・定性目標の組み合わせ（合計100%）</p>
            </div>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          {performanceGoals.length === 0 && (
            <div className="text-gray-400 text-sm text-center py-8">
              目標がありません。「定量目標追加」または「定性目標追加」ボタンで目標を追加してください。
            </div>
          )}
          {performanceGoals.map((goal, index) => (
            <div key={goal.id} className="border rounded-lg p-4 space-y-4">
              <div className="flex items-center justify-between">
                <h4 className="font-medium">
                  {goal.type === 'quantitative' ? '定量目標' : '定性目標'} {index + 1}
                </h4>
                <div className="flex items-center space-x-2">
                  <select
                    value={goal.type}
                    onChange={e => updateGoal(goal.id, "type", e.target.value as GoalType)}
                    className="text-sm border rounded px-2 py-1"
                  >
                    <option value="quantitative">定量目標</option>
                    <option value="qualitative">定性目標</option>
                  </select>
                  {performanceGoals.length > 1 && (
                    <Button 
                      variant="ghost" 
                      size="sm" 
                      onClick={() => removeGoal(goal.id)}
                      className="text-red-600"
                    >
                      <Minus className="w-4 h-4" />
                    </Button>
                  )}
                </div>
              </div>

              <div className="grid grid-cols-4 gap-4">
                <div>
                  <Label htmlFor={`weight-${goal.id}`}>ウエイト (%)</Label>
                  <Input
                    id={`weight-${goal.id}`}
                    type="number"
                    min="0"
                    max="100"
                    value={goal.weight}
                    onChange={e => updateGoal(goal.id, "weight", parseInt(e.target.value) || 0)}
                    className="mt-1"
                  />
                </div>
                <div className="col-span-3">
                  <Label htmlFor={`specific-${goal.id}`}>具体的目標 *</Label>
                  <Input
                    id={`specific-${goal.id}`}
                    value={goal.specificGoal}
                    onChange={e => updateGoal(goal.id, "specificGoal", e.target.value)}
                    placeholder="具体的で明確な目標を記載してください"
                    className="mt-1"
                  />
                </div>
              </div>

              <div>
                <Label htmlFor={`criteria-${goal.id}`}>達成基準 *</Label>
                <Textarea
                  id={`criteria-${goal.id}`}
                  value={goal.achievementCriteria}
                  onChange={e => updateGoal(goal.id, "achievementCriteria", e.target.value)}
                  placeholder="どのような状態になれば達成とみなすかを明確に記載してください"
                  className="mt-1"
                />
              </div>

              <div>
                <Label htmlFor={`methods-${goal.id}`}>手段・手法 *</Label>
                <Textarea
                  id={`methods-${goal.id}`}
                  value={goal.methods}
                  onChange={e => updateGoal(goal.id, "methods", e.target.value)}
                  placeholder="目標達成のための具体的な手段や手法を記載してください"
                  className="mt-1"
                />
              </div>
            </div>
          ))}
        </CardContent>
        {/* CardContentの下に進捗バーと追加ボタンを配置 */}
        <div className="px-6 pb-6 pt-2 space-y-3">
          <div className="space-y-2">
            <div className="flex justify-between text-sm">
              <span>ウエイト配分進捗</span>
              <span className={isWeightValid ? "text-green-600" : "text-red-600"}>
                {totalPerformanceWeight}/100%
              </span>
            </div>
            <Progress 
              value={totalPerformanceWeight} 
              className={`h-2 ${totalPerformanceWeight > 100 ? 'bg-red-100' : ''}`}
            />
            {!isWeightValid && (
              <p className="text-sm text-red-600 flex items-center">
                <AlertTriangle className="w-4 h-4 mr-1" />
                ウエイト合計が100%になるよう調整してください
              </p>
            )}
          </div>
          <div className="flex items-center space-x-2">
            <Badge variant={isWeightValid ? "default" : "destructive"}>
              合計: {totalPerformanceWeight}%
            </Badge>
            <Button variant="outline" size="sm" onClick={() => addGoal("quantitative")}>
              <Plus className="w-4 h-4 mr-1" />
              定量目標追加
            </Button>
            <Button variant="outline" size="sm" onClick={() => addGoal("qualitative")}>
              <Plus className="w-4 h-4 mr-1" />
              定性目標追加
            </Button>
          </div>
        </div>
      </Card>
    </div>
  );
}
