'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import clsx from 'clsx';
import React from 'react';
import { groups } from "@/components/constants/routes";
import { IoHome } from 'react-icons/io5';
import { LuGoal } from 'react-icons/lu';
import { FaClipboardCheck } from 'react-icons/fa';
import { MdComment } from 'react-icons/md';
import { FaUserCog } from 'react-icons/fa';
import { VscGraph } from 'react-icons/vsc';

const iconMap: Record<string, React.ReactElement<{ size?: number }>> = {
  '/user-management': <FaUserCog />,
  '/report': <VscGraph />,
  '/goal-approval': <FaClipboardCheck />,
  '/evaluation-feedback': <MdComment />,
  '/goal-input': <LuGoal />,
  '/evaluation-input': <IoHome />,
};

export default function Sidebar() {
  const pathname = usePathname();

  return (
    <aside className="w-[64px] hover:w-[200px] min-h-screen h-full bg-primary text-primary-foreground transition-all duration-300 ease-in-out group shadow-[10px_0_10px_rgba(0,0,0,0.1)] relative">
      <nav>
        {groups.map((g) => (
          <div key={g.title} className="border-b border-sidebar-border p-[8px]">
            {g.links.map((l) => (
              <Link
                key={l.href}
                href={l.href}
                className={clsx(
                  'flex items-center p-[8px] rounded-lg text-sm',
                  pathname === l.href
                    ? 'bg-primary text-accent hover:bg-primary'
                    : 'hover:bg-primary/80'
                )}
              >
                <div
                  className={clsx(
                    "w-[24px] h-[24px]",
                    pathname === l.href
                      ? "text-accent"
                      : "text-primary-foreground"
                  )}
                >
                  {iconMap[l.href] && React.cloneElement(iconMap[l.href], { size: 24 })}
                </div>
                <span
                  className={clsx(
                    "ml-[12px] text-[15px] opacity-0 group-hover:opacity-100 font-semibold transition-opacity delay-100 whitespace-nowrap",
                    pathname === l.href
                      ? "text-accent"
                      : "text-primary-foreground"
                  )}
                >
                  {l.label}
                </span>
              </Link>
            ))}
          </div>
        ))}
      </nav>
    </aside>
  );
}
